img {
  display: block;
  // outline: 1px solid green;
}

.gallery {
  padding-top: 60px;
  display: grid;
  //     // max-width: 320px;

  //     // outline: 1px solid yellow;
  //    grid-template-columns: 290px;
  // grid-template-rows:  repeat(20, 443px);
  // row-gap: 30px;

  justify-items: center;
  justify-content: center;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(20, 443px);
  grid-column-gap: 0px;
  grid-row-gap: 10px;

  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(2, 293px);
    grid-template-rows: repeat(10, 440px);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }

  @media screen and (min-width: 1024px) {
    grid-template-columns: repeat(3, 274px);
    grid-template-rows: repeat(7, 440px);
  }
}

.card {
  // margin: auto;
  // box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
  // 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  // border: 1px solid black;
  width: 280px;
  height: 443px;
  // margin-bottom: 100px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    cursor: pointer;
    transform: scale(1.035);
  }
}
.card__image {
  height: 398px;
}

.genres {
  display: flex;
}

.card__info {
  margin-bottom: 20px;
  margin-top: 5px;

  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
}

.info__genres {
  display: flex;

  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: orangered;
}
.genres__item:not(:last-child):after {
  content: ',';
  margin-right: 5px;
}

.card__date::before {
  content: '|';
  margin-right: 5px;
  margin-left: 5px;
}

